@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.dark {
  @apply bg-background-dark text-text-dark;
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded-md transition-all duration-300 font-medium;
  }
  
  .btn-primary {
    @apply bg-primary-light dark:bg-primary-dark text-white hover:opacity-80;
  }

  .section-title {
    @apply text-3xl font-bold mb-6 text-text-light dark:text-text-dark;
  }
}